<template>
    <div>
        <user-provider :detailed=true :id="id">
            <template slot-scope="{ user }">
                <h3>{{ user.username }}</h3>
                <div class="follow-box" v-if="!isSelf" >
                    <span v-if="isFriend" >followed ✔️</span>
                    <button v-else @click="follow" > {{loading ? 'loading' : 'Follow'}} </button>
                </div>
                <div class="is-you" v-else >
                    <h4>It's you 🤠</h4>
                </div>
                <div class="friends">
                  <h4>Follows</h4>
                  <user-summaries v-if="user.friends" :ids="user.friends.map(friend => friend.id)" />
                </div>
                <div class="checkins">
                    <checkin-list v-if="user.check_ins" :checkins="user.check_ins" />
                </div>
            </template>
        </user-provider>
    </div>
</template>

<script>
import UserProvider from '../providers/UserProvider.vue'
import UserSummaries from '../containers/UserSummaries'
import CheckinList from '../containers/CheckinList.vue'
import { follow } from '../../database/queries.js'

export default {
  props: {
    id: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    isSelf() {
      return this.$store.state.userInfo.id === this.id
    },
    isFriend() {
      return this.$store.state.userInfo.friends.includes(this.id)
    },
  },
  components: {
    UserProvider,
    CheckinList,
    UserSummaries,
  },
  methods: {
    async follow() {
      this.loading = true
      try {
        const { data: { addFriend } } = await this.$apollo.mutate({
          mutation: follow,
          variables: {
            id: this.id,
          },
        })
        this.$store.commit('setUserInfo', addFriend)
      } catch (err) {
        console.log(err)
      } finally {
        this.loading = false
      }
    },
  },
}
</script>

